export default {
	data() {
		return {
			showLoginBox:1,
			userInfo:{},
			fullLogin:0,
			parentObj:''
		}
	},
	props: {
		type: {
			type: String,
			default: 'login'
		},
		showLogin: {
			type: String,
			default: '1'
		},
		onlyLogin: {
			type: String,
			default: '0'
		},
		loadData:function(type){
		    this.$emit("pLoadData")
		},
	},
	created() {
		this.userInfo = uni.getStorageSync('userInfo');
	},
	methods: {
		toggleMask(obj){
			this.showLoginBox = 1;
			this.fullLogin = 1;
			this.parentObj = obj;
		},
		getUserInfo(res){
			var _this = this;
			if (!res.detail.iv) {
				uni.showToast({
					title: "您取消了授权,登录失败",
					icon: "none"
				});
				return false;
			}
			uni.login({
				provider: 'weixin',
				success: function(loginRes) {
					uni.getUserInfo({
						provider: 'weixin',  
						withCredentials:true,  
						success: function (infoRes) {  
							uni.request({
								url: 'https://td.clmsj.com/clmsj/login/index', // http://bull.hnw.net/index/bull/client_wash_info
								data: {
									encrypt_data:encodeURIComponent(infoRes.encryptedData),
									iv:encodeURIComponent(infoRes.iv),
									code:loginRes.code,
									type:2,  //type   1专业版  2零售版
								},
								method:'POST',
								success: (res) => {
									if (res.data.code == 0) {
										uni.setStorageSync("session_key",res.data.auth);
										uni.setStorageSync("userInfo",res.data.data.user);
										_this.showLoginBox = 0;
										if(_this.parentObj == 'loadData'){
											_this.$emit("pLoadData")
										}
									}else{
										uni.showToast({
											icon:'none',
											title:'请重新获取信息...'
										});
									}
								},
							})
						},  
						fail:function(infoRes){  
							console.log('infoRes='+JSON.stringify(infoRes))
						}  
					});
				},
			});
		},
		hideLoginBox(){
			this.showLoginBox = 0;
		},
		eventClick(){
			this.$emit('eventClick');
		}
	},
}
